import { fallbackValue } from './fallback-value';

/**
 * 
 * @param target 
 * @param extractor 
 * @param defaultFieldValue 
 */
export function getSafeObjProp<T, U = any>(
  target: U,
  extractor: (obj: U) => T | undefined | null,
  defaultFieldValue: T
) {
  return fallbackValue(extractor(target || {} as U), defaultFieldValue);
}
