import { Component, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { CeuiAccordionItemChange, CeuiAccordionOption } from '@ceui/core/components';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AppComponent {
  sessionKey = 'componentList';

  componentList: CeuiAccordionOption[] = [
    {
      title: ``,
      isExpand: true,
      children: [
        {
          title: `Alert `,
          active: true,
          router: `alert`,
        },
        {
          title: `Button `,
          router: `button`,
        },
        {
          title: `Card `,
          router: `card`,
        },
        {
          title: `Dialog `,
          router: `dialog`,
        },

        {
          title: `Icon `,
          router: `icon`,
        },
        {
          title: `Tabs `,
          router: `tabs`,
        },
        {
          title: `Toast `,
          router: `toast`,
        },
        {
          title: `Tooltip `,
          router: `tooltip`,
        },
      ],
    },
    {
      title: ``,
      isExpand: true,
      children: [
        {
          title: `Accordion `,
          router: `accordion`,
        },
      ],
    },
    {
      title: ``,
      isExpand: true,
      children: [
        {
          title: `Cascader `,
          router: `cascader`,
        },
        {
          title: `Checkbox `,
          router: `checkbox`,
        },
        {
          title: `FormField `,
          router: `form-field`,
        },
        {
          title: `Input `,
          router: `input`,
        },
        {
          title: `Radio `,
          router: `radio`,
        },
      ],
    },
  ];

  constructor(private _router: Router) {
    const cache = sessionStorage.getItem(this.sessionKey);
    if (typeof cache === 'string') {
      try {
        this.componentList = JSON.parse(cache);
      } catch (error) {
      }
    }
  }

  onItemChange(event: CeuiAccordionItemChange) {
    this._router.navigateByUrl(`${event.item.router}`);
    sessionStorage.setItem(this.sessionKey, JSON.stringify(this.componentList));
  }
}
